import Vue from 'vue'

// const HOST = 'http://192.168.1.10:3000'
const HOST = 'http://egoend1.tunnel.qydev.com'
export default {
  install () {
    Vue.prototype.$apis = {
      login: '/login',
      logout: '/logout',
      register: '/register', // 注册
      answer: '/question/answer', // answer question
      userInfo: '/user/info', // 用户信息
      todayQuestion: '/question/today', // view today question  parmas:{isToday: true}
      getRandomCode: '/getRandomCode', // question params {isToday=true}
      updatePassword: '/updatePassword', // reset password
      giftList: '/userGift/list',  // gift list
      questionStoreList: '/questionstore/list', // user right question wrong question params {userId}
      questionById: '/question/byId', // get one question by id  params{questionId}
      getGiftById: '/gift/getById', // get one question by id  params{questionId}
      saveBaseInfo: 'user/baseInfo', // save base address user info phone
      redeemGift: '/gift/redeem', // redeem gift now
      redeemGift2: '/gift/redeem2', // redeem gift now 2
      userGetRedeemed: '/gift/user/redeemedGifts', // user get redeemed gift by userId
      getExpressInfo: '/user/expressInfo',  // get express info by userId
      getRedeemOrderById: '/gift/user/getRedeemOrderById',  // get redeemOrderById info by orderId
      userOffline: HOST + '/front/dUser/Offline', // 查看我的下线
      userGrade: HOST + '/front/dUserRole/query', // 选择用户会员级别
      upGrade: HOST + '/front/dUserRole/query', // 升级会员
      getCode: HOST + '/front/dUser/getCode', // 选获取手机验证码  参数 token Phone
      getProvice: HOST + '/Fmodules/dProvinces/query', // 查看省份 无参数
      getCity: HOST + '/Fmodules/dCity/query', // 查看城市 参数：provinceid
      getDistrict: HOST + '/Fmodules/dArea/query', // 查看城市 参数：District
      transform: HOST + '/front/dUser/transfer', // 转账 参数：token  type 1 转奖金 2转注册币 3转激活币   money 钱    account 转给谁的帐号？
      convert: HOST + '/front/dUser/convert', // 转换 参数：token type=1 奖金币转注册币,type=2 注册币转奖金币
      addBankCard: HOST + '/front/DBank/add', // 添加银行卡 参数 bankName帐号 account开户名 name开户地址  address
      myBankCard: HOST + '/front/DBank/query', // 查看绑定的银行卡
      deleteBankCard: HOST + '/front/DBank/delete', // 删除银行卡 参数 token 银行卡  bankId
      withdraw: HOST + '/front/dUser/withdraw', // 提现 银行卡ID bankId 提现的钱 money token
      withdrawLog: HOST + '/front/dWithdrawalApplication/query', // 提现明细 参数  token 第几页 pageNo  每页几条 pageSize
      netUsers: HOST + '/front/dLayerFigure/query',  // 网络图谱
      netQueryById: HOST + '/front/dLayerFigure/queryByAccount',  // 根据用户查看下线参数 account  ,token
      myMessage: HOST + '/front/message/query',  // 查看留言 参数token
      sendMessage: HOST + '/front/message/send',  // 发送留言  参数 token  remark userAccount
      convertAndTransLogs: HOST + '/front/dRevenueRecord/query', // 参数  token type2 1为转换  2为转账  pageNo pageSize
      deposit: HOST + '/front/dPay/deposit',  // 充值 参数 token type  1；奖金，2注册，3激活 现在只能充激活币 money ,note 备注 ,tupian 图片一张 可以不传
      depositLogs: HOST + '/front/dPay/query',  // 充值记录 参数 token   state  1：未处理 ，2:通过，3：不通过  pageNo  pageSize
      orderUsers: HOST + '/front/dUser/queryFrom',  // 查看报单会员，参数token， type ，pageNo PsgeSize
      products: HOST + '/modules/dProduct/query',  // 商品数据  参数id
      activeateRadio: HOST + '/front/dUser/queryActivateRatio',   // 激活时注册币使用最高的比例 参数 token ，account
      activeAccount: HOST + '/front/dUser/activation', // 激活用户 //token, account, registerCurrency, activateCurrency
      withdrawRadio: HOST + '/modules/dRule/query',  // 提现比例
      revenueRecord: HOST + '/front/dRevenueRecord/query'  // 奖金币，注册币，激活币 记录 ，type 1;奖金币，2注册币，3激活币

    }
  }
}



