
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Drawer from 'vux/src/components/drawer/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { mapState, mapActions } from 'vuex'

export default {
directives: {
TransferDom
},
components: {
Radio,
Group,
Cell,
Badge,
Drawer,
ButtonTab,
ButtonTabItem,
ViewBox,
XHeader,
Tabbar,
TabbarItem,
Loading,
Actionsheet
},
methods: {
onIndexChange (newIndex, oldIndex) {
console.log(newIndex, oldIndex)
},
onShowModeChange (val) {
/** hide drawer before changing showMode **/
this.drawerVisibility = false
setTimeout(one => {
this.showModeValue = val
}, 400)
},
onPlacementChange (val) {
/** hide drawer before changing position **/
this.drawerVisibility = false
setTimeout(one => {
this.showPlacementValue = val
}, 400)
},
onClickMore () {
this.showMenu = true
},
changeLocale (locale) {
this.$i18n.set(locale)
this.$locale.set(locale)
},
...mapActions([
'updateDemoPosition'
])
},
mounted () {
console.log(this.$store.state.loading.isLoading)
this.handler = () => {
if (this.path === '/demo') {
this.box = document.querySelector('#demo_list_box')
this.updateDemoPosition(this.box.scrollTop)
}
}
},
beforeDestroy () {
this.box && this.box.removeEventListener('scroll', this.handler, false)
},
watch: {
path (path) {
if (path === '/component/demo') {
this.$router.replace('/demo')
return
}
if (path === '/demo') {
setTimeout(() => {
this.box = document.querySelector('#demo_list_box')
if (this.box) {
this.box.removeEventListener('scroll', this.handler, false)
this.box.addEventListener('scroll', this.handler, false)
}
}, 1000)
} else {
this.box && this.box.removeEventListener('scroll', this.handler, false)
}
}
},
computed: {
...mapState({
route: state => state.route,
path: state => state.route.path,
deviceready: state => state.app.deviceready,
demoTop: state => state.vux.demoScrollTop,
isLoading: store => store.loading.isLoading,
direction: state => state.loading.direction
}),
isShowBar () {
if (/component/.test(this.path)) {
return true
}
return false
},
leftOptions () {
return {
showBack: this.route.path !== '/',
backText: ''
}
},
rightOptions () {
return {
showMore: false
}
},
headerTransition () {
if (!this.direction) return ''
return this.direction === 'forward' ? 'vux-header-fade-in-right' : 'vux-header-fade-in-left'
},
componentName () {
if (this.route.path) {
const parts = this.route.path.split('/')
if (/component/.test(this.route.path) && parts[2]) return parts[2]
}
},
isDemo () {
return /component|demo/.test(this.route.path)
},
isTabbarDemo () {
return /tabbar/.test(this.route.path)
},
title () {
if (this.route.path === '/') return 'Good luck!'
if (this.route.path === '/login') return '登 录'
if (this.route.path === '/signUp') return '注 册'
if (this.route.path === '/updatePassword') return '修改密码'
if (this.route.path === '/home') return 'Home'
if (this.route.path === '/questionlist') return '已回答'
if (this.route.path === '/question') return '问答'
if (this.route.path === '/questionDetail') return '问答'
if (this.route.path === '/questionDetail') return '问题'
if (this.route.path === '/redeem') return '礼品🎁'
if (this.route.path === '/redeemDetail') return '已兑换🎁'
if (this.route.path === '/productRedeemDetail') return '兑换详情🎁'
if (this.route.path === '/productDetail') return '兑换🎁'
if (this.route.path === '/paygift') return '支付🎁'
if (this.route.path === '/addressInfo') return '快递信息'
if (this.route.path === '/personCenter') return '个人中心'
if (this.route.path === '/setting') return '设 置'
if (this.route.path === '/withdraw') return '提 现'
if (this.route.path === '/donate') return '☕️☕️'
if (this.route.path === '/starme') return '关注微信公众号'
if (this.route.path === '/redeemInstruction') return '兑换说明'
return this.componentName ? `/${this.componentName}` : 'Oh no page!'
},
viewTransition () {
if (!this.direction) return ''
return 'vux-pop-' + (this.direction === 'forward' ? 'in' : 'out')
}
},
data () {
return {
showMenu: false,
loadingText: 'Loading...',
menus: {
'language.noop': '<span class="menu-title">Language</span>',
'zh-CN': '中文',
'en': 'English'
},
drawerVisibility: false,
showMode: 'push',
showModeValue: 'push',
showPlacement: 'left',
showPlacementValue: 'left'
}
}
}



