import * as types from '../mutation'

// initial state
const state = {
  isLoading: false,
  direction: 'forward',
  user: {},
  token: null,
  userAccount: null,
  bonusCurrency: null,
  userId: '',
  newPassword: ''
}

// getters

// actions

// mutations
const mutations = {
  // 我们可以使用 ES2015 风格的计算属性命名功能来使用一个常量作为函数名
  [types.UPDATE_LOADING] (state, status) {
    state.isLoading = status
  },
  [types.UPDATE_DIRECTION] (state, direction) {
    state.direction = direction
  },
  [types.LOGIN]: (state, data) => {
    localStorage.token = data
    state.token = data
  },
  [types.LOGOUT]: (state) => {
    localStorage.removeItem('token')
    state.token = null
  },
  [types.USERID]: (state, data) => {
    localStorage.userId = data
    state.userId = data
  },
  [types.NEWPASSWORD]: (state, data) => {
    state.newPassword = data
  },
  [types.USERACCOUNT]: (state, data) => {
    state.userAccount = data
  },
  [types.BONUS]: (state, data) => {
    state.bonusCurrency = data
  }
}

export default {
  state,
  mutations
}



