
import Vuex from 'vuex'
import Vue from 'vue'
// import * as actions from './actions'
import login from './modules/login'
import loading from './modules/loading'

Vue.use(Vuex)

const debug = process.env.NODE_ENV !== 'production'

export default new Vuex.Store({
  modules: {
    login,
    loading
  },
  strict: debug
})



